import React, { useEffect, useState } from "react";
import { Dialog, Divider } from "@mui/material";
import style from "./LibraryModal.module.css";
import { useStaffApproveArrayQuery } from "../../../../services/qvipleAPI";
import { useTranslation } from "react-i18next";
import { imageShowUrl } from "../../../../services/BaseUrl";
import { useNavigate } from "react-router-dom";
import { libraryChangeAction } from "../../../../redux/store/library-slice";
import {
  useAssignLibraryHead,
  useEditLibraryHead,
} from "../../../../hooks/member_tab/library-api";
import { useDispatch, useSelector } from "react-redux";
import { useInView } from "react-intersection-observer";
import ButtonWithAction from "../../../../Custom/Button/ButtonWithAction";
import { useOneInstituteDashboard } from "../../../../hooks/dashboard/dashboard-api";

function Item({ img, photoId, name, index, setIndex, assignAdmissionHead }) {
  const ClickAdmission = () => {
    assignAdmissionHead();
    setIndex(index);
  };

  return (
    <div className={style.sidebarbottomitem} onClick={ClickAdmission}>
      <img
        alt="not found"
        src={
          photoId !== "1"
            ? `${imageShowUrl}/${img}`
            : "/images/demo_users/user1.svg"
        }
      />
      <div>
        <p className={style.namee}>{name}</p>
        <p className={style.desig}>{index}</p>
      </div>
    </div>
  );
}

function LibraryModal({
  libraryModal,
  hideModal,
  edit,
  librarian,
  onParentRefetch,
}) {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const ids = useSelector((state) => state.idChange);
  const [noMoreResults, setNoMoreResults] = useState(false);
  const [timeeout, setTimeeout] = useState(false);
  const [page, setPage] = useState(1);
  const [allStaff, setAllStaff] = useState([]);
  const [ref, inView] = useInView();
  const [assignLibraryHead] = useAssignLibraryHead();
  const [editLibraryHead] = useEditLibraryHead();
  const dispatch = useDispatch();

  const { data: sportsArtsStaff, refetch: sportsArtsStaffRefetch } =
    useStaffApproveArrayQuery(
      {
        id: ids?.id,
        page: page,
        limit: 10,
      },
      { skip: !ids?.id }
    );
  useEffect(() => {
    if (ids?.id) {
      sportsArtsStaffRefetch();
    }
  }, [ids?.id, sportsArtsStaffRefetch]);

  const { oneInstituteDashboard, oneInstituteDashboardRefetch } =
    useOneInstituteDashboard(
      {
        id: ids?.id,
      },
      { skip: !ids?.id }
    );

  useEffect(() => {
    if (ids?.id) {
      oneInstituteDashboardRefetch();
    }
  }, [ids?.id, oneInstituteDashboardRefetch]);
  useEffect(() => {
    if (sportsArtsStaff) {
      if (sportsArtsStaff.staffIns) {
        setAllStaff([...allStaff, ...sportsArtsStaff.staffIns]);
      } else if (page > 1) {
        setNoMoreResults(true);
      }
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [sportsArtsStaff]);

  useEffect(() => {
    if (inView) {
      if (!noMoreResults) {
        setPage(page + 1);
      }
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [inView]);

  useEffect(() => {
    setTimeout(() => {
      setTimeeout(!timeeout);
    }, 3000);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const navigate = useNavigate();
  const [assign, setAssign] = useState(false);
  const [clicked, setClicked] = useState(false);
  const [sportsHead, setSportsHead] = useState({
    sid: "",
    name: "",
    index: "",
    photoId: "",
    profilePhoto: "",
  });

  const libraryQuery = () => {
    if (ids?.id !== "") {
      setDisabled(true);
      assignLibraryHead({
        activate: {
          sid: sportsHead?.sid ?? "",
        },
        id: ids?.id,
      }).then((res) => {
        if (res.data.status) {
          setDisabled(false);
          dispatch(
            libraryChangeAction.libraryQuery({
              lid: res.data?.library,
              lhead: sportsHead.sid,
            })
          );
          navigate(`/q/${oneInstituteDashboard?.institute?.name}/library`);
        }
      });
    }
  };

  const editLibraryQuery = () => {
    if (sportsHead.sid && librarian?._id) {
      setDisabled(true);
      editLibraryHead({
        osid: librarian?._id,
        nsid: sportsHead.sid,
      }).then((res) => {
        dispatch(
          libraryChangeAction.libraryQuery({
            lid: res.data?.library,
            lhead: sportsHead.sid,
          })
        );
        onParentRefetch();
        setSportsHead({ ...sportsHead, sid: "" });
        setClicked(!clicked);
        setAssign(!assign);
        setDisabled(false);
        hideModal();
      });
    }
  };

  const filterStaff = allStaff?.filter((item) => {
    return item?._id !== librarian?._id;
  });

  return (
    <Dialog open={libraryModal} onClose={() => hideModal(false)}>
      <div className={style.admissionModal}>
        <div className={style.admissionModalhead}>
          <h6>{edit ? t("edit_library") : t("activate_library")}</h6>
          <img
            onClick={() => hideModal(false)}
            src="/images/close-post-icon.svg"
            alt=""
          />
        </div>
        <Divider />
        <div className={style.admissionModalbody}>
          {!assign && !edit && (
            <div
              className={style.assignDesignation}
              onClick={() => setAssign(true)}
            >
              <img
                className={style.assignManager}
                src="/images/sports/avatar-circle.svg"
                alt=""
              />
              <div>
                <h6>{t("assign_lib_head")}</h6>
                <p>{t("search_from_joined_list")}</p>
              </div>
            </div>
          )}

          {!assign && edit && (
            <div
              className={style.sidebarbottomitem}
              onClick={() => setAssign(true)}
            >
              <img
                alt="not found"
                src={
                  librarian?.photoId !== "1"
                    ? `${imageShowUrl}/${librarian?.staffProfilePhoto}`
                    : "/images/demo_users/user1.svg"
                }
              />
              <div>
                <p className={style.namee}>{`${
                  librarian?.staffFirstName || ""
                } ${
                  librarian?.staffMiddleName ? librarian?.staffMiddleName : ""
                } ${librarian?.staffLastName || ""}`}</p>
                <p className={style.desig}>
                  {" "}
                  {t("index_no")} {librarian?.index}
                </p>
              </div>
            </div>
          )}

          {assign && !clicked && (
            <>
              <div className={style.searchbar}>
                <input
                  type="text"
                  placeholder={t("search")}
                  className={`form-control ${style.searchinput}`}
                />
                <img
                  className={style.searchIcon}
                  alt="img"
                  src="/images/search-dash-icon.svg"
                />
              </div>

              <div className={style.items}>
                {edit
                  ? filterStaff?.map((d) => (
                      <>
                        <Item
                          img={d.staffProfilePhoto}
                          photoId={d.photoId}
                          name={`${d.staffFirstName} ${
                            d.staffMiddleName ? d.staffMiddleName : ""
                          } ${d.staffLastName}`}
                          index={d.staffROLLNO}
                          setIndex={(w) => setClicked(true)}
                          assignAdmissionHead={() => {
                            setSportsHead({
                              ...sportsHead,
                              sid: d?._id,
                              name: `${d?.staffFirstName} ${
                                d?.staffMiddleName ? d?.staffMiddleName : ""
                              } ${d?.staffLastName}`,
                              index: d?.staffROLLNO,
                              photoId: d?.photoId,
                              profilePhoto: d?.staffProfilePhoto,
                            });
                          }}
                        />
                        <Divider />
                      </>
                    ))
                  : allStaff?.map((d) => (
                      <>
                        <Item
                          img={d.staffProfilePhoto}
                          photoId={d.photoId}
                          name={`${d.staffFirstName} ${
                            d.staffMiddleName ? d.staffMiddleName : ""
                          } ${d.staffLastName}`}
                          index={d.staffROLLNO}
                          setIndex={(w) => setClicked(true)}
                          assignAdmissionHead={() => {
                            setSportsHead({
                              ...sportsHead,
                              sid: d?._id,
                              name: `${d?.staffFirstName} ${
                                d?.staffMiddleName ? d?.staffMiddleName : ""
                              } ${d?.staffLastName}`,
                              index: d?.staffROLLNO,
                              photoId: d?.photoId,
                              profilePhoto: d?.staffProfilePhoto,
                            });
                          }}
                        />
                        <Divider />
                      </>
                    ))}
              </div>
            </>
          )}
          {assign && clicked && (
            <div className={style.sidebarbottomitem}>
              <img
                alt="not found"
                src={
                  sportsHead.photoId !== "1"
                    ? `${imageShowUrl}/${sportsHead.profilePhoto}`
                    : "/images/demo_users/user1.svg"
                }
              />
              <div>
                <p className={style.namee}>{sportsHead.name}</p>
                <p className={style.desig}>Index No. {sportsHead.index}</p>
              </div>
            </div>
          )}
        </div>
        <div className={style.admissionModalfooter}>
          <div>
            <ButtonWithAction
              disabled={disabled}
              shwoLoader={true}
              buttonText={edit ? t("edit_") : t("activate")}
              customStyleButton={{
                minHeight: "2rem",
                marginTop: "0rem",
              }}
              onAction={edit ? editLibraryQuery : libraryQuery}
            />
          </div>
        </div>
      </div>
    </Dialog>
  );
}

export default LibraryModal;
